import styles from './styles.module.css'

export default function PropertyName ({ property, schema, required = false }) {
  return (
    <>
      <span className={styles.propertyName} title={property}>
        {property}
        {required && <span className={styles.requiredIndicator}>*</span>}
      </span>
      {schema.deprecatedBy && (
        <sub className={styles.propertyDeprecation}>
          (deprecated)
        </sub>
      )}
    </>
  )
}
